import * as React from 'react'
import {Route} from 'react-router-dom'
import About from '../pages/about/about'
import Home from '../pages/home/home'

export default class AppRoutes extends React.Component {

  public static pageTitle = 'over-react-tspages'
  public static links = {
    about: `/${AppRoutes.pageTitle}/about`,
    home: `/${AppRoutes.pageTitle}/`
  }

  public myHome = () => {
    return <Home text={'hello!'} testNumber={100}/>
  }

  public render() {
    return (
      <span>
        <Route key='' exact={true} path={`${AppRoutes.links.home}`} render={this.myHome}/>
        <Route key='' exact={true} path={`${AppRoutes.links.about}`} component={About}/>
      </span>
    )
  }
}
